<h1 id="command-line-commands" tabindex="-1"><a class="header-anchor" href="#command-line-commands" aria-hidden="true">#</a> command-line commands</h1>
<p><strong>Yarn</strong> 提供一系列丰富的命令行命令，这些命令在使用Yarn包的时候，包括安装，管理，发布等提供了一些列的帮助。</p>
<TOC />
<h2 id="yarn-add" tabindex="-1"><a class="header-anchor" href="#yarn-add" aria-hidden="true">#</a> yarn add</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">yarn</span> <span class="token function">add</span> <span class="token operator">&lt;</span>package<span class="token operator">></span> -D
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="yarn-remove" tabindex="-1"><a class="header-anchor" href="#yarn-remove" aria-hidden="true">#</a> yarn remove</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">yarn</span> remove <span class="token operator">&lt;</span>package<span class="token punctuation">..</span>.<span class="token operator">></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>运行<code>yarn remove foo</code>会从依赖中移除<code>foo</code>这个包，更新<code>package.json</code>和<code>yarn.lock</code>文件。</p>
<p>其他的开发者可以通过运行<code>yarn install</code>去同步自己的<code>node_modules</code>目录</p>
<p>当移除一个包，会移除所有类型的依赖，比如<code>dependencies</code>，<code>devDependencies</code></p>
<div class="custom-container tip"><p class="custom-container-title">TIP</p>
<p><code>yarn remove</code>会更新<code>package.json</code>和<code>yarn.lock</code>文件。这样确保开发同一项目的不同的开发者都有相同的依赖。</p>
</div>
<div class="custom-container tip"><p class="custom-container-title">TIP</p>
<p><code>yarn remove &lt;package&gt; --flag</code>和<code>yarn install</code>使用相同的<code>flag</code>参数</p>
</div>
